<template>
  <div class="login">
    <header>
      <div class="logobox">
        <div class="logo"></div>
        <div class="logontag">Orange</div>
      </div>
      <router-link class="logintxt" to="/home">
        <van-icon class="cancel"  name="cross" size="22px" color="#000" />
      </router-link>
      
    </header>

    <div class="inputBox">
      <div class="introduce">Welcome to Orange Movie!</div>
      <!-- 登录页面 -->
      <div class="loginmodule">
        <van-cell-group :border="false">
            <van-field type="text" v-model="user.value"
            right-icon="question-o" placeholder="请输入用户名" 
            :error-message="user.errorMsg"
             @input="validCommon(user.value,'validUsername',{key:'user',errorMsg:'该用户名不存在或输入错误'})" 
            :border="false"/>

            <van-field  v-model="pwd.value" :type="pwd.type"
            placeholder="请输入密码" :right-icon="pwd.icon" 
            :error-message="pwd.errorMsg"
            @click-right-icon="checkPwd('pwd')"
            @input="validCommon(pwd.value,'validPwd',{key:'pwd',errorMsg:'用户名或密码错误'})" 
            :border="false"/>

            <van-button class="loginBtn" :loading="isLoading" 
                type="info" block loading-text="登录中..." 
                text="登录" @click="login" :disabled="!isValid"
            />

            <div class="forgetpwd">
              <router-link  to="/register" class="registertxt" > 
                <span class="fl">创建用户</span>
              </router-link>
              <router-link  to="/forgetPwd" class="registertxt" > 
                <span class="fr">忘记密码</span>
              </router-link>
            </div>
        </van-cell-group>
      </div>
      <!-- 其他登录方式 -->
      <div class="another">
            <div class="with">
                <div>—— 第三方账号登录 ——</div>
                <ul class="clearfix">
                    <li>
                        <div class="other_login qq"></div>
                    </li>
                    <li>
                        <div class="other_login wechat"></div>
                    </li>
                    <li>
                        <div class="other_login weibo"></div>
                    </li>
                    <li>
                        <div class="other_login facebook"></div>
                    </li>
                </ul>
            </div>
      </div>

      <footer>
        <span>注册代表您同意该软件
          <a href="">用户服务协议</a>和
          <a href="">隐私政策</a>
        </span>
      </footer>

    </div>

  </div>
</template>

<script>
import {validForm} from '../tool/validForm'

import { createNamespacedHelpers } from "vuex";

//基于loginModule的命名空间导入mapState、mapMutations 辅助函数
const {mapState,mapMutations} = createNamespacedHelpers('loginModule');

export default {
  name: "login",

  // 计算属性
  computed:{
    // 数组的导入方式
    ...mapState(['user','pwd','isValid','isLoading'])
  },

  methods:{
    // 查看密码及修改状态
    ...mapMutations(['checkPwd']),

    // 登录验证  
    login(){
        // console.log('登录');
        let self = this;

        self.$store.commit('loginModule/login',true); //登录

        // 5S内随机时间
        let randomTime = Math.random() * 3 * 1000;

        //登录延时
        setTimeout(() =>{

        // 4获取所有用户信息(一开没有登录始拿不到)
        let allusersInfo = localStorage.getItem('allusersInfo');
        allusersInfo = allusersInfo ? JSON.parse(allusersInfo) : []; // 判断有没有值否则会报错一个null
        // console.log('allusersInfo=>',allusersInfo);

        // 如果用户不存在
        if(allusersInfo.length === 0){
            self.$toast({
                duration: 2000,
                message: '该用户不存在' //配置参数Toast 如果存在就还是先延迟2s再截断
            })
            self.$store.commit('loginModule/login', false);
            return;
        }

        // 查找用户
        for(let i = 0; i<allusersInfo.length; i++){

          // 用户名匹配成功
            if(allusersInfo[i].username == self.user.value){
                
                // 用户存在核对成功匹配密码
                if(allusersInfo[i].pwd == self.pwd.value){
                  self.$toast({
                      duration:2000, // 转2秒就停止
                      message:'登录成功'
                  })
                  self.$store.commit('loginModule/login',false);
                  
                  //跳转到首页
                  //记录用户登录状态
                  let user = {
                    isLogin: true,
                    username: this.user.value
                  };
                  localStorage.setItem('user', JSON.stringify(user));
                  return this.$router.push({name: 'home'});
                  // console.log('user=>',user);

                }else{
                    self.$toast({
                        duration:2000, // 转2秒就停止
                        message:'用户或密码不正确'
                    })
                    self.$store.commit('loginModule/login',false);
                    return;
                }
            }
        }

        //用户不存在
        this.$toast({
          duration: 2000,
          message: '该用户不存在'
        })
        this.$store.commit('loginModule/login', false);

      },randomTime)

    },

    //验证用户名或者密码！！！！！！
    validCommon(value,fnName,o){
        // console.log('value=>',value); //是否有值传入
        // o.fnName就是validForm.vaildPwd 和 validForm.validUsername
        // let isValid = validForm[o.fnName](v); 将fnName作为形参传递

        let isValid = validForm[fnName](value);
        // console.log('isValid=>',isValid);

        o.isValid = isValid;
        this.$store.commit('loginModule/validCommon',o);
        this.$store.commit('loginModule/passFun');
    }
    
  }

};
</script>

<style lang="less" scoped>
  .login{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    // background:url("../assets/images/bg7.png") no-repeat center center;
    header{
      padding: 30px 15px;
      .logobox{
        width: 60px;
        height: 60px;
        background-color: #fff;
        border-radius: 50%;
        .logo{
          width: 60px;
          height: 60px;
          border-radius: 50%;
          background: url("../assets/images/orangeblack.png") no-repeat center center;
          background-size: 50px 50px;
        }
        .logontag{
          font-size: 22px;
          color:#000; 
          font-family: "华文行楷";
          font-weight: bold;
          text-align: center;
          text-indent: 40px;
          margin-top: -8px;
        }
      }
      .cancel{
        position: absolute;
        right: 20px;
        top: 20px;
      }
    }

    .inputBox{
      padding: 15px;
      .introduce{
        font-size: 18px;
        color:#000; 
        font-weight: bold;
        padding: 6px 0;
        text-indent: 10px;
      }
      .loginmodule{
        padding: 10px;
      }
      .van-cell-group {
        padding:14px;
        // background: #C8EFD4;
        background-color: transparent;
        // border: 1px solid #5773A9;
        // border-radius: 8px;
        // box-shadow: 1px 1px 4px 1px #ddd;

      }
      .van-cell {
        // background-color: transparent;
        // border: 1px solid #ddd;
        margin: 16px 0;
        border-radius: 10px;
        box-shadow: 1px 1px 4px 1px #ddd;

      }

      .forgetpwd{
        width: 90%;
        margin: 16px auto 0;
        color: #888;
        font-size: 14px;
        .registertxt{
          margin: 20px 30px 0px 0px;
          color: #888;
        }
      }

      /deep/.van-button.loginBtn {
        width: 100%;
        border-radius: 10px;
        margin: 40px auto 0px;
        color: #fff;
        background-color:#aaa;
        // background-image: repeating-linear-gradient(
        //   to right,
        //   #fd4154,
        //   #fe7278,
        //   #fe959b
        // );
        font-size: 18px;
        border: none;
        
        &:hover{
         background-color:#F32F6F; 
         color: #fff;
        }
      }
      .van-button--disabled {
        opacity: 0.9; 
      }
    }

    .another{
        padding:10px;
        >div{
            padding: 0 40px;
            margin: 0 auto;
        }
        .with{
            width:65%;
            >div{
                text-align: center;
                font-size: 14px;
                padding-bottom: 10px;
                color: #999;
            }
        }
        ul li{
            float: left;
            width:25%;
            height:50px;
            .other_login{
                width: 50px;
                height: 50px;
                margin: 0 auto;
                // background-color: #fff;
                border-radius: 50%;
                &:active{
                  border-right:2px solid #ddd;
                  border-bottom:2px solid #ddd;
                }
            }
            .qq{
              background: url("../assets/images/social_697A9C.png") no-repeat -9px -9px;
              &:active{
                background: url("../assets/images/social_color.png") no-repeat -9px -9px;
              }
            }
            .wechat{
              background: url("../assets/images/social_697A9C.png") no-repeat -78px -9px;
                
                &:active{
                  background: url("../assets/images/social_color.png") no-repeat -78px -9px;
              }
            }
            .weibo{
              background: url("../assets/images/social_697A9C.png") no-repeat -9px -78px;
                
                &:active{
                  background: url("../assets/images/social_color.png") no-repeat -9px -78px;
              }
            }
            .facebook{
              background: url("../assets/images/social_697A9C.png") no-repeat -78px -78px;
                            
                &:active{
                  background: url("../assets/images/social_color.png") no-repeat -78px -78px;
              }
            }
        }
    }

    footer{
      width: 100%;
      position: fixed;
      bottom: 20px;
      text-align: center;
    }
  }


</style>
